/*--------------------------------------------------------------
## Menus
--------------------------------------------------------------*/
.main-menu {
  background: #990000;
}

@media (min-width: 992px) {
  .main-menu {
    background: #fcd209;
  }
}

.main-navigation {
  clear: both;
  display: none;
  float: left;
  width: 100%;
  text-transform: uppercase;
  text-align: center;
}

@media (min-width: 992px) {
  .main-navigation {
    height: 56px;
  }
}

.main-navigation ul {
  list-style: none;
  margin: 0;
  margin-left: -15px;
  padding-left: 0;
  display: block;
  width: 100%;
  background: #fcd209;
  position: absolute;
}

@media (min-width: 992px) {
  .main-navigation ul {
    background: none;
    margin-left: 0;
    width: auto;
    position: relative;
  }
}

.main-navigation ul ul {
  display: none;
  top: 100%;
  z-index: 99999;
  background: #cc9900;
}

@media (min-width: 992px) {
  .main-navigation ul ul {
    display: block;
    float: left;
    position: absolute;
    left: -999em;
  }
}

.main-navigation ul ul ul {
  left: -999em;
  top: 0;
}

.main-navigation ul ul li {
  border-top: 1px solid #fff;
}

.main-navigation ul ul li:hover {
  background: #b38600;
}

.main-navigation ul ul li:hover > ul,
.main-navigation ul ul li.focus > ul {
  left: 100%;
}

.main-navigation ul ul a {
  width: 200px;
  padding: 16px 25px;
}

.main-navigation ul ul .current_page_item > a,
.main-navigation ul ul .current-menu-item > a {
  background: #660000;
}

.main-navigation ul li:hover > ul,
.main-navigation ul li.focus > ul {
  left: 0;
}

.main-navigation li {
  position: relative;
}

.main-navigation li:last-child {
  border-right: none;
}

.main-navigation li:hover, .main-navigation li.focus {
  background: #cc9900;
}

@media (min-width: 992px) {
  .main-navigation li {
    border-right: 1px solid #fff;
    float: left;
  }
}

.main-navigation a {
  display: block;
  text-decoration: none;
  color: #ffffff;
  padding: 16px 24px;
}

@media (min-width: 992px) {
  .main-navigation a {
    padding: 16px 16px;
  }
}

@media (min-width: 1200px) {
  .main-navigation a {
    padding: 16px 24px;
  }
}

.main-navigation .current_page_item > a,
.main-navigation .current-menu-item > a,
.main-navigation .current_page_item > a:hover,
.main-navigation .current-menu-item > a:hover,
.main-navigation .current_page_ancestor > a,
.main-navigation .current-menu-ancestor > a,
.main-navigation .current-menu-ancestor > a:hover {
  background: #990000;
}

.main-navigation .current-menu-item:hover ul,
.main-navigation .current_page_ancestor ul {
  background: #990000;
}

.main-navigation .current-menu-item:hover ul li:hover,
.main-navigation .current_page_ancestor ul li:hover {
  background: maroon;
}

/* Small menu. */
.menu-toggle {
  border: none;
  background: transparent;
  box-shadow: none;
  width: 60px;
  height: 45px;
  position: relative;
  margin: 10px 0;
  float: right;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: .5s ease-in-out;
  -moz-transition: .5s ease-in-out;
  -o-transition: .5s ease-in-out;
  transition: .5s ease-in-out;
  cursor: pointer;
}

.menu-toggle:hover, .menu-toggle:active, .menu-toggle:focus {
  border: none;
  background: transparent;
  box-shadow: none;
  outline: none;
}

.menu-toggle span {
  display: block;
  position: absolute;
  height: 9px;
  width: 50%;
  background: #fcd209;
  opacity: 1;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: .25s ease-in-out;
  -moz-transition: .25s ease-in-out;
  -o-transition: .25s ease-in-out;
  transition: .25s ease-in-out;
}

.menu-toggle span:nth-child(even) {
  left: 50%;
  border-radius: 0 9px 9px 0;
}

.menu-toggle span:nth-child(odd) {
  left: 0px;
  border-radius: 9px 0 0 9px;
}

.menu-toggle span:nth-child(1), .menu-toggle span:nth-child(2) {
  top: 0px;
}

.menu-toggle span:nth-child(3), .menu-toggle span:nth-child(4) {
  top: 18px;
}

.menu-toggle span:nth-child(5), .menu-toggle span:nth-child(6) {
  top: 36px;
}

.menu-toggle.open span:nth-child(1), .menu-toggle.open span:nth-child(6) {
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  transform: rotate(45deg);
}

.menu-toggle.open span:nth-child(2), .menu-toggle.open span:nth-child(5) {
  -webkit-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -o-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

.menu-toggle.open span:nth-child(1) {
  left: 5px;
  top: 7px;
}

.menu-toggle.open span:nth-child(2) {
  left: calc(50% - 5px);
  top: 7px;
}

.menu-toggle.open span:nth-child(3) {
  left: -50%;
  opacity: 0;
}

.menu-toggle.open span:nth-child(4) {
  left: 100%;
  opacity: 0;
}

.menu-toggle.open span:nth-child(5) {
  left: 5px;
  top: 29px;
}

.menu-toggle.open span:nth-child(6) {
  left: calc(50% - 5px);
  top: 29px;
}

.menu-toggle,
.main-navigation.toggled ul {
  display: block;
  overflow: hidden;
}

@media screen and (min-width: 992px) {
  .menu-toggle {
    display: none;
  }
  .main-navigation {
    display: block;
  }
  .main-navigation ul {
    display: inline-block;
  }
}

.site-main .comment-navigation, .site-main
.posts-navigation, .site-main
.post-navigation {
  margin: 0 0 1.5em;
  overflow: hidden;
}

.comment-navigation .nav-previous,
.posts-navigation .nav-previous,
.post-navigation .nav-previous {
  float: left;
  width: 50%;
}

.comment-navigation .nav-next,
.posts-navigation .nav-next,
.post-navigation .nav-next {
  float: right;
  text-align: right;
  width: 50%;
}

/*--------------------------------------------------------------
# Accessibility
--------------------------------------------------------------*/
/* Text meant only for screen readers. */
.screen-reader-text {
  clip: rect(1px, 1px, 1px, 1px);
  position: absolute !important;
  height: 1px;
  width: 1px;
  overflow: hidden;
}

.screen-reader-text:focus {
  background-color: #f1f1f1;
  border-radius: 3px;
  box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
  clip: auto !important;
  color: #21759b;
  display: block;
  font-size: 14px;
  font-size: 0.875rem;
  font-weight: bold;
  height: auto;
  left: 5px;
  line-height: normal;
  padding: 15px 23px 14px;
  text-decoration: none;
  top: 5px;
  width: auto;
  z-index: 100000;
  /* Above WP toolbar. */
}

/* Do not show the outline on the skip link target. */
#content[tabindex="-1"]:focus {
  outline: 0;
}

/*--------------------------------------------------------------
# Alignments
--------------------------------------------------------------*/
.alignleft {
  display: inline;
  float: left;
  margin-right: 1.5em;
}

.alignright {
  display: inline;
  float: right;
  margin-left: 1.5em;
}

.aligncenter {
  clear: both;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

/*--------------------------------------------------------------
# Clearings
--------------------------------------------------------------*/
.clear:before,
.clear:after,
.entry-content:before,
.entry-content:after,
.comment-content:before,
.comment-content:after,
.site-header:before,
.site-header:after,
.site-content:before,
.site-content:after,
.site-footer:before,
.site-footer:after {
  content: "";
  display: table;
  table-layout: fixed;
}

.clear:after,
.entry-content:after,
.comment-content:after,
.site-header:after,
.site-content:after,
.site-footer:after {
  clear: both;
}

/*--------------------------------------------------------------
# Widgets
--------------------------------------------------------------*/
.widget {
  margin: 0 0 1.5em;
  /* Make sure select elements fit in widgets. */
}

.widget select {
  max-width: 100%;
}

/*--------------------------------------------------------------
# Content
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Content Sidebar
--------------------------------------------------------------*/
.site-description {
  background-color: rgba(0, 102, 51, 0.9);
  display: inline-block;
  font-size: 36.8px;
  font-size: 2.3rem;
  color: #fff;
  font-weight: lighter;
  padding: 43px 50px;
}

.content-area {
  float: left;
  margin: 0 -25% 0 0;
  width: 100%;
}

.site-main {
  margin: 0 25% 0 0;
}

.site-content .widget-area {
  float: right;
  overflow: hidden;
  width: 25%;
}

.site-footer {
  clear: both;
  width: 100%;
}

/*--------------------------------------------------------------
## Posts and pages
--------------------------------------------------------------*/
.sticky {
  display: block;
}

.hentry {
  margin: 0 0 1.5em;
}

.byline,
.updated:not(.published) {
  display: none;
}

.single .byline,
.group-blog .byline {
  display: inline;
}

.page-content,
.entry-content,
.entry-summary {
  margin: 1.5em 0 0;
}

.page-links {
  clear: both;
  margin: 0 0 1.5em;
}

/*--------------------------------------------------------------
## Comments
--------------------------------------------------------------*/
.comment-content a {
  word-wrap: break-word;
}

.bypostauthor {
  display: block;
}

/*--------------------------------------------------------------
# Infinite scroll
--------------------------------------------------------------*/
/* Globally hidden elements when Infinite Scroll is supported and in use. */
.infinite-scroll .posts-navigation,
.infinite-scroll.neverending .site-footer {
  /* Theme Footer (when set to scrolling) */
  display: none;
}

/* When Infinite Scroll has reached its end we need to re-display elements that were hidden (via .neverending) before. */
.infinity-end.neverending .site-footer {
  display: block;
}

/*--------------------------------------------------------------
# Media
--------------------------------------------------------------*/
.page-content .wp-smiley,
.entry-content .wp-smiley,
.comment-content .wp-smiley {
  border: none;
  margin-bottom: 0;
  margin-top: 0;
  padding: 0;
}

/* Make sure embeds and iframes fit their containers. */
embed,
iframe,
object {
  max-width: 100%;
}

/*--------------------------------------------------------------
## Captions
--------------------------------------------------------------*/
.wp-caption {
  margin-bottom: 1.5em;
  max-width: 100%;
}

.wp-caption img[class*="wp-image-"] {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.wp-caption .wp-caption-text {
  margin: 0.8075em 0;
}

.wp-caption-text {
  text-align: center;
}

/*--------------------------------------------------------------
## Galleries
--------------------------------------------------------------*/
.gallery {
  margin-bottom: 1.5em;
}

.gallery-item {
  display: inline-block;
  text-align: center;
  vertical-align: top;
  width: 100%;
}

.gallery-columns-2 .gallery-item {
